:root {
    --dark-bg: #2f2f2f;
    --light-bg: #FEFEFE;
    --component-bg-dark: #616161;
    --calendar-border-dark: #34FFC8;
    --component-bg-light: #707070;
    --calendar-border-light: #838485;
}

body {
    background-color: var(--dark-bg);
    color: white;
    font-family: 'Open Sans', sans-serif;
    font-size: 3em;
}

button {
    background-color: var(--calendar-border-light);
    padding: 10px 15px;
    font-size: 1.2em;
    border: none;
    border-radius: 10px;
    margin-left: 10px;
}
button:hover {
    background-color: var(--calendar-border-dark);
}