.grid {
    display: grid;
}

.weather-info {
    display: flexbox;
    text-align: center;
}

.weather-info input {
    border-radius: 20px;
    border: none;
    padding: 10px 10px;
    text-align: right;
    background-color: var(--component-bg-dark);
    color: white;
    font-size: 1.5em;
    margin-bottom: 15px;
 }

.weather-info img {
    vertical-align: middle;
}


.imgs {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.imgs img {
    border-radius: 10px;
}




figcaption {
    width: 100px;
    display: block;
    }
    
    h2 { 
    font-size: 12px;
    display: flex;
    }
    
      p { 
    font-size: 14px;
    }


td {
    width: 50%;
    border-bottom: 1px solid black;
    padding: 15px;
}

.extra-info {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.extra-info span {
    padding-bottom: 10px;
    border-bottom: 1px solid black;
    line-height: 48px;
    border-collapse: collapse;
}




