.switch {
    position: relative;
    width: 60px;
    height: 34px;
    float: right;
    

    input {
        opacity: 0;
  width: 0;
  height: 0;
  
    }
    
      
      input:checked+ .slider .on {
        display: block;
      }
      
      input:checked + .slider .off {
        display: none;
      }
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: var(--component-bg-light);
        -webkit-transition: .4s;
        transition: .4s;
      }
      
      .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
      }
      
      input:checked + .slider {
        background-color: var(--calendar-border-dark);
      }
      
      input:focus + .slider {
        box-shadow: 0 0 1px white;
      }
      
      input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
      }
      
      /* Rounded sliders */
      .slider.round {
        border-radius: 34px;
      }
      
      .slider.round:before {
        border-radius: 50%;
      }
  }
